.container {
	position: fixed;
	right: 0px;
	max-width: 360px;
}

.logo {
	display: flex;
	justify-content: center;
}

.logoContent {
	width: 80px;
	height: 36px;
}

.search {
	display: flex;
	align-items: center;
	margin-top: 7px;
	width: 100%;
	height: 28px;
	border-bottom-color: #f1f1f1;
	border-bottom-style: solid;
	border-bottom-width: 1px;
}

.searchIcon {
	display: flex;
	width: 12px;
	height: 12px;
}

.searchInput {
	outline: none;
	border-style: none;
	height: 100%;
	flex-grow: 1;
	padding-right: 8px;
	padding-left: 8px;
	color: #d7e0e7;
	font-family: Source Sans Pro;
	font-size: 14px;
	font-weight: 600;
}

.searchInput::placeholder {
	color: #d7e0e7;
}

.tabs {
	position: relative;
	margin-top: 18px;
	padding-right: 20px;
	padding-left: 20px;
	height: 22px;
}

.tabsList {
	position: relative;
	z-index: 1;
	display: flex;
	height: calc(100% - 1px);
}

.tabsListItem {
	font-family: Source Sans Pro;
	font-size: 14px;
	line-height: 1.14em;
	flex-grow: 1;
	cursor: pointer;
	text-align: center;
}

.tabsListItem:focus {
	border-bottom-style: solid;
	border-bottom-width: 4px;
	outline: 0;
}

.tabsListItemSelected {
	border-bottom-style: solid;
	border-bottom-width: 4px;
	outline: 0;
}

.tabsListItemAll {
	color: #6d7c8b;
}

.tabsListItemAll:focus {
	border-bottom-color: #738190;
}

.tabsListItemAlerts {
	color: #fa5680;
}

.tabsListItemAlerts:focus {
	border-bottom-color: #fa5680;
}

.tabsListItemNotifications {
	color: #80c17d;
}

.tabsListItemNotifications:focus {
	border-bottom-color: #80c17d;
}

.tabsListItemGeneral {
	color: #2ba6df;
}

.tabsListItemGeneral:focus {
	border-bottom-color: #2ba6df;
}

.tabsDivider {
	position: relative;
	bottom: 1px;
	height: 1px;
	border-bottom-color: #7e92a3;
	border-bottom-style: solid;
	border-bottom-width: 1px;
}

.body {
	margin-top: 9px;
	margin-right: auto;
	margin-left: auto;
	max-height: 500px;
	overflow-y: scroll;
}

.body::-webkit-scrollbar {
	width: 6px;
}

.body::-webkit-scrollbar-track {
	background: none;
}

.body::-webkit-scrollbar-thumb {
	background: #7e92a3;
	border-radius: 3px;
}

.bodyItem {
	position: relative;
	padding-top: 9px;
	padding-left: 70px;
	padding-right: 68px;
	padding-bottom: 9px;
	z-index: 1;
}

.bodyItem:hover,
.bodyItem:focus {
	background-color: #f6fafd;
	cursor: pointer;
}

.bodyItem:focus {
	outline: none;
}

.bodyItem::before {
	position: absolute;
	top: 12px;
	left: 48px;
	content: '';
	display: block;
	border-radius: 12px;
	width: 12px;
	height: 12px;
}

.bodyItemAlert::before {
	background-color: #fa5680;
}

.bodyItemNotification::before {
	background-color: #80c17d;
}

.bodyItemGeneral::before {
	background-color: #2ba6df;
}

.bodyItemButton {
	position: relative;
	display: none;
	align-items: center;
	position: absolute;
	width: 24px;
	height: 100%;
	top: 0px;
	right: 27px;
}

.bodyItem:hover > .bodyItemButton,
.bodyItem:focus > .bodyItemButton {
	display: flex;
}

.bodyItemButtonIcon {
	position: relative;
	width: 24px;
	height: 24px;
	transition: transform 0.5s;
}

.bodyItemButtonIcon::after {
	position: absolute;
	top: 0;
	left: 0;
	content: '';
	width: 24px;
	height: 24px;
}

.bodyItemContent {
	color: #536476;
	font-family: Source Sans Pro;
	font-size: 14px;
	line-height: 1.14em;
	text-align: left;
}

.bodyItemTimestamp {
	margin-top: 3px;
	color: #cccccc;
	font-family: Source Sans Pro;
	font-size: 12px;
	font-weight: 400;
	line-height: 1.2em;
	text-align: left;
}

.button {
	display: flex;
	border-style: none;
	justify-content: center;
	align-items: center;
	background-color: #80c17d;
	width: 100%;
	height: 50px;
	color: #ffffff;
	font-size: 16px;
	margin-top: 17px;
}

.button:hover {
	background-color: #72ad70;
}

.menuListBackdrop {
	position: fixed;
	top: 0px;
	left: 0px;
	width: 100vw;
	height: 100vh;
}

.menuList {
	position: fixed;
	z-index: 1;
	border-radius: 6px;
	width: 192px;
	background-color: #ffffff;
	padding-top: 13px;
	padding-bottom: 11px;
	box-shadow: 0 4px 13px 0 rgba(0, 0, 0, 0.2);
}

.menuListItem {
	padding: 5px 18px 5px 15px;
	color: #536476;
	font-family: Source Sans Pro;
	font-size: 14px;
	line-height: 19px;
	cursor: pointer;
}

.menuListItem:hover {
	background-color: #f6fafd;
}
