.widget {
	text-align: center;
	min-width: 360px;
	min-height: 600px;
	padding: 70px 36px !important;
}

.noBorder {
	border: none !important;
	box-shadow: none !important;
}

.introduction.qbitsText {
	max-width: 224px;
	margin: 16px auto 26px auto;
	font-size: 16px;
	line-height: 21px;
}

.circles {
	height: 250px;
	margin-bottom: 14px;
}

.popup .circles {
	margin-left: 50px;
}

.textBubble {
	width: 100%;
	border-radius: 9px;
	border: solid 1px #dde4eb;
	background-color: #f1f1f1;
	color: #98a2ac;
	text-align: left;
	padding: 1px 15px 1px 20px;
	box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.2);
	font-family: Source Sans Pro;
	font-weight: 400;
}

.notificationsCircle {
	display: flex;
	justify-content: center;
	transform: translateX(calc(-68px - 8px));
}

.notificationsCircle svg {
	width: 136px;
	height: 136px;
}

.notificationsCircle::after {
	position: absolute;
	z-index: -1;
	content: '';
	display: block;
	border-radius: 82px;
	background-color: #fa5680;
	width: 82px;
	height: 82px;
	transform: translate(calc(50% + 82px + 45px), 43px);
}

.notificationsCircle .textBubble {
	position: absolute;
	margin-top: 0px;
	width: 224px;
	padding: 12px 20px;
	line-height: 1.2em;
	transform: translate(calc(50% - 68px + 8px), 15px);
}

.actionsCircle {
	display: flex;
	justify-content: center;
	transform: translate(calc(41px + 45px), calc(-136px + 43px));
}

.actionsCircle svg {
	visibility: hidden;
	width: 82px;
	height: 82px;
}

.actionsCircle .textBubble {
	position: absolute;
	margin-top: 0px;
	width: 224px;
	padding: 12px 20px;
	line-height: 1.2em;
	transform: translate(calc(50% - 41px - 120px), 43px);
}

.generalCircle {
	display: flex;
	justify-content: center;
	transform: translate(17px, calc(-82px - 136px + 155px));
}

.generalCircle svg {
	width: 84px;
	height: 84px;
}

.generalCircle .textBubble {
	position: absolute;
	margin-top: 0px;
	width: 224px;
	padding: 12px 20px;
	line-height: 1.2em;
	transform: translate(calc(50% - 42px - 82px), -3px);
}

.qbitsLogo {
	width: 180px;
	height: 80px;
}

.qbitsText {
	margin-bottom: 16px;
	font-family: 'Source Sans Pro', sans-serif;
	text-align: center;
	color: #536476;
	font-size: 20px;
}

.qbitsTextBottom {
	margin-bottom: 14px;
	font-family: 'Source Sans Pro', sans-serif;
	text-align: center;
	color: #536476;
	font-size: 20px;
}

.button {
	margin-right: 0px !important;
}
