.buttonRow {
	display: flex !important;
	flex-direction: row;
	align-content: space-between;
	flex-wrap: nowrap;
	padding-top: 5px;
}

.buttonRow > * {
	flex-grow: 1;
	flex-shrink: 1;
}

.buttonRow > :first-child {
	margin-right: 10px;
}

.buttonRow > :not(:first-child):not(:last-child) {
	margin-left: 10px;
	margin-right: 10px;
}

.buttonRow > :last-child {
	margin-left: 10px;
	flex-grow: 0;
	flex-shrink: 1;
}
