.mciLabel {
	font-size: 14px;
	line-height: 17px;
	font-family: 'Source Sans Pro', sans-serif !important;
	align-items: left;
	color: #536476;
	font-weight: 400;
}
.infoIcon {
	vertical-align: top !important;
}
.mciDonutContainer {
	display: inline-block;
}
.block {
	display: inline-block;
}

.mciLabelText {
	display: inline-block;
	vertical-align: middle;
	padding-right: 5px;
	top: 15%;
	transform: translateY(-160%);
}

.mciLabelTextVertical {
	text-align: center;
}

.mciLabelContainer {
	display: inline-block;
	width: 130px;
}
.mciToolTip {
	display: inline-block;
	padding-top: 1em;
	width: 60%;
}
.mciLabelWideContainer {
	display: inline-block;
	width: 150px;
}
.mciLabelWideContainer p {
	margin-bottom: 0.1em !important;
	width: 100%;
}
.mciLabelContainer p {
	margin-bottom: 0.1em !important;
	width: 100%;
}
.labelText {
	justify-content: left;
}
.currencyText {
	font-weight: 600;
	justify-content: right !important;
	float: right;
}

.mciLabelVerticalContainer {
	/*text-align: center;*/
	padding-left: 20%;
	padding-right: 20%;
}

/*
 * source: https://css-tricks.com/centering-in-the-unknown/
 */
.donutVerticalContainer {
	display: table;
	width: 100%;
}

.donutVerticalContainer * {
	display: table-cell;
	text-align: center;
	vertical-align: middle;
}
