:global(.ui.sidebar) {
	background-color: #ecf2f6;
	height: calc(100% - 60px) !important;
}

:global(.pushable:not(body)) {
	width: 100%;
	transform: none !important;
	overflow: visible;
}

:global(.pusher) {
	overflow: unset !important;
}

:global(.pushable > .pusher.dimmed):after {
	min-height: calc(100% - 60px);
	opacity: 0 !important;
}

.sidebarContainer {
	margin: 0 5% 20% 5%;
}

.sidebarTitle {
	pointer-events: none;
	display: inline-flex;
	background-color: #7e92a3;
	width: 100%;
	margin-bottom: 1em;
}
.sidebarTitleText {
	display: inline;
	padding-left: 1em;
	padding-top: 0.5em;
	color: #ffffff !important;
}

.sidebarClose {
	display: inline;
	margin-left: auto;
	padding-top: 1em;
	padding-right: 1em;
	z-index: 12 !important;
	cursor: pointer;
}

.closeButton {
	cursor: pointer;
	z-index: 12 !important;
	position: absolute;
	top: 0.8em;
	right: 1em;
}

.staticState {
	position: fixed !important;
	top: 60px !important;
	width: 633px !important;
	box-shadow: -10px 10px 20px rgba(34, 36, 38, 0.15) !important;
}
