.linkButton {
	padding: 0 !important;
	background-color: transparent !important;
	color: #80c17d !important;
}

.linkButton:hover,
.linkButton:focus {
	color: #80c17d !important;
	text-decoration: underline;
	opacity: 0.9;
}

.bigLinkButton:global(.button) {
	font-size: 15px;
	line-height: 19px;
}
